.dialog-container {

  .subtitle-1 {
    line-height: 1em;

    .app-version {
      position: absolute;
      right: 10px;
      top: 10px;
      font-size: 12px;
    }
  }

  mat-dialog-actions {
    justify-content: space-around;
    margin-bottom: -0.5rem;
  }

  .mat-dialog-content {
    height: 85vh;
    max-height: unset !important;
  }

  mat-dialog-content {
    overflow-y: auto;

    table {
      width: 100%;
      margin: 0 0 1rem 0;

      th, td {
        text-align: center;
        padding: 0.2rem;
        font-weight: normal;
        font-size: 13px;
      }

      th:first-child, td:first-child {
        text-align: left;
      }
    }
  }
}
